				checkboxes_child: [],
				terms: [
					{
						id: 1,
						isConfirm: false,
						content: '1',
					},
					{
						id: 2,
						isConfirm: false,
						content: '2',
					},
					{
						id: 3,
						isConfirm: false,
						content: '3',
					},
				],
				confirmAgrees: [
					{
						conf_tit_id: 1,
						title: '',
						required: true,
						conf_list: [
							{
								conf_sub_id: 1,
								title:
									' 14 . ( 14   .)',
								hasMoreView: false,
							},
							{
